body {
    background-color: rgb(234, 237, 237);
}

.payment {
    background-color: white;
}

.payment__container > h1 {
    text-align: center;
    padding: 10px;
    font-weight: 400;
    background-color: rgb(234, 237, 237);
    border-bottom: 1px solid lightgray;
}

.payment__container > h1 a {
    text-decoration: none;
}

.payment__section {
    display: flex;
    padding: 20px;
    margin: 0 20px;
    border-bottom: 1px solid lightgray;
}

.payment__title {
    flex: 0.2;
}

.payment__address, .payment_items, .payment__details {
    flex: 0.8;
}

.payment__details > form {
    max-width: 400px;
}

.payment__details > h4 {
    padding-bottom: 20px;
}

.payment__details > form > div > button {
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    font-weight: bolder;
    margin: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}

.payment__details > form > div > button:active{
    background-color: white;
    box-shadow: 0 0.5px ;
    transform: translateY(1px);
}

@media only screen and (max-width: 720px) {
    .payment__section {
        display: flex;
        flex-direction: column;
    }
    .payment__details {
        margin-left: auto;
        margin-right: auto;
        justify-content: center;
        width: 80%;
    }
    .payment__total {
        max-width: 100%;
    }
}